@tailwind base;
@tailwind components;
@tailwind utilities;
.v-color-theme {
  color: #3072a6; }

.v-color-font {
  color: #000; }

.v-color-subtext {
  color: #adadad; }

.v-bgcolor-theme {
  background: #3072a6; }

.v-bgcolor-title {
  background: #f7f7f7; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-weight: normal;
  font-family: "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  color: #000;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: 'liga', 'kern';
  text-rendering: optimizeLegibility;
  background: #fff no-repeat;
  word-wrap: break-word;
  font-size: 16; }

p {
  padding-bottom: 1rem; }

.cm {
  box-sizing: border-box;
  background: repeating-linear-gradient(45deg, rgba(245, 245, 245, 0.1), rgba(245, 245, 245, 0.1) 10px, rgba(136, 136, 136, 0.2) 0, rgba(136, 136, 136, 0.2) 20px);
  border: solid 1px rgba(68, 68, 68, 0.2); }

@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10  */
  .ie-block {
    display: block; }
  .ie-hidden {
    display: none; } }

.check-mode * [class^='ly_'] {
  outline: #dc143c 2px solid; }

.check-mode * [class^='bl_'] {
  border: #00f 2px solid; }

.check-mode * [class^='el_'] {
  border: #ffd700 2px solid; }

.check-mode-outer * [class^='ly_'] {
  outline: #dc143c 2px solid; }

.check-mode-bl * [class^='bl_'] {
  border: #00f 2px solid; }

.check-mode-el * [class^='el_'] {
  border: #ffd700 2px solid; }

