h2.c-tail,
.h2.c-tail {
  @apply flex flex-row items-center relative pl-4 pt-2 pb-2 pr-2 border-b-2 font-bold text-3xl text-white;
  background-color: #424242; }
  h2.c-tail.with-margin,
  .h2.c-tail.with-margin {
    @apply mt-12 mb-8; }

h3.c-tail,
.h3.c-tail {
  @apply border border-black pl-4 pt-2 pb-2 pr-2 font-bold text-3xl;
  box-shadow: 2px 2px 0 0 black;
  color: #424242; }
  h3.c-tail.with-margin,
  .h3.c-tail.with-margin {
    @apply mt-12 mb-8; }

h4.c-tail,
.h4.c-tail {
  @apply pl-4 py-2 text-xl font-bold relative;
  border-bottom: dashed 1px #a0aec0;
  color: #424242; }
  h4.c-tail::before,
  .h4.c-tail::before {
    position: absolute;
    content: '';
    left: 0;
    top: 60%;
    transform: translateY(-50%);
    width: 4px;
    height: 60%;
    border-top: solid 1.5rem #3182ce; }
  h4.c-tail.with-margin,
  .h4.c-tail.with-margin {
